import React, { useState } from 'react';
import CopyToClipboard from 'react-copy-to-clipboard';
import { Button, Table } from 'semantic-ui-react';
import { SectionHeader } from '../../uiComponents/Headers';
import { IEBPSColumns, IFormFieldList } from '../interfaces';
import { AddReadOnlyType } from './AddReadOnlyType';
import { TEMPLATE_DELIMETER } from './utils/constants';
import { TReadOnlyTypeObject } from './utils/types/setupTypes';

export const DataPoints = ({
	dataMapping,
	readOnlyTypes,
	setReadOnlyTypes,
}: {
	readOnlyTypes: TReadOnlyTypeObject[];
	setReadOnlyTypes: React.Dispatch<React.SetStateAction<TReadOnlyTypeObject[]>>;
	dataMapping: (IFormFieldList & { details: IEBPSColumns })[];
}) => {
	const [open, setOpen] = useState(false);
	const [editData, setEditData] = useState<TReadOnlyTypeObject>();

	const handleClose = () => {
		setOpen(false);
	};

	const handleEdit = (id: number) => {
		const editData1 = readOnlyTypes.find((row) => row.id === id);
		if (editData1) {
			setEditData(editData1);
		}
		setOpen(true);
	};
	return (
		<div>
			<SectionHeader>
				<h4>Available Data Points</h4>
			</SectionHeader>
			<Table striped compact="very" size="small">
				<Table.Header>
					<Table.Row>
						<Table.HeaderCell>Data Point</Table.HeaderCell>
						<Table.HeaderCell>Copy</Table.HeaderCell>
					</Table.Row>
				</Table.Header>
				<Table.Body>
					{dataMapping.map((data) => (
						<>
							{!data.ebpsColumnId ? (
								<Table.Row key={data.id}>
									<Table.Cell>{data.name}</Table.Cell>
									<Table.Cell>
										{data.details ? (
											<CopyToClipboard
												text={`[[data${TEMPLATE_DELIMETER}${data.details.columnName}${
													data.vocabularyId ? `${TEMPLATE_DELIMETER}${data.vocabularyId}` : ''
												}]]`}
												onCopy={() => {}}
											>
												<Button title="Copy" primary icon="copy" compact size="tiny" />
											</CopyToClipboard>
										) : null}
									</Table.Cell>
								</Table.Row>
							) : null}
						</>
					))}
					{readOnlyTypes.map((data) => (
						<Table.Row key={data.id}>
							<Table.Cell>{data.name}</Table.Cell>
							<Table.Cell>
								<CopyToClipboard text={`[[${data.type}${TEMPLATE_DELIMETER}${data.meta}]]`} onCopy={() => {}}>
									<Button primary icon="copy" type="button" compact size="tiny" />
								</CopyToClipboard>
								<Button
									className="primary-btn"
									onClick={handleEdit.bind(null, data.id || 0)}
									title="Edit"
									icon="edit"
									size="tiny"
									compact
								/>
							</Table.Cell>
						</Table.Row>
					))}
				</Table.Body>
			</Table>
			<AddReadOnlyType open={open} handleClose={handleClose} setReadOnlyTypes={setReadOnlyTypes} editData={editData} />
		</div>
	);
};

